* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html,
body {
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #f85032;
  background: linear-gradient(to top, #961d1d, rgb(238, 214, 36), #2f60db);
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
  color: antiquewhite;
  background-image: url(/fcc-quote-machine/static/media/bookshelf.f678c371.jpg);
  background-size: cover;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f10a;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(145, 87, 87, 0.623);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(209, 193, 193);
}

::-webkit-input-placeholder {
  color: rgb(226, 194, 152);
}

.app-container {
  position: relative;
  height: 65vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 15vh;
}

.searches-container {
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.quoteBox-app-container {
  border-radius: 1%;
  box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.842),
    -2px 0px 12px rgba(0, 0, 0, 0.842);
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.842);
  position: absolute;
  left: 50%;
  top: 20%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 50%;
  height: 75%;
  min-height: 200px;
  overflow-y: auto;
  padding: 10px 20px;
  font-size: 33px;
}

.quoteBox-app-container:hover .quote-info-container {
  border-bottom: 1px solid rgba(255, 255, 0, 0.644);
}

@media only screen and (max-width: 744px) {
  .searches-container {
    display: block;
    margin-top: -60px;
  }

  .quoteBox-app-container {
    top: 35%;
    width: 75%;
  }
}

@media only screen and (max-width: 446px) {
  .quoteBox-app-container {
    width: 100%;
    height: 85%;
  }
}

@media only screen and (max-height: 650px) {
  .quoteBox-app-container {
    height: 95%;
  }
}

.search-container {
  width: 33.33%;
  margin: 10px 20px;
  border: 2px solid transparent;
  transition: 0.5s;
}

.search-container:hover {
border: 2px solid rgb(209, 164, 17)
}

.search-container input {
  width: 100%;
  font-size: 25px;
  border: none;
  border-radius: 3%;
  padding: 5px;
  background-color: rgba(20, 20, 20, 0.904);
  color: rgb(235, 208, 174);
}


@media only screen and (max-width: 744px) {
  .search-container {
    width: 90%;
  }
}
.quoteBox-container {
  /* display: inline-block; */
}

.quote-info-container {
  font-size: 30px;

  height: 220px;
  overflow-y: auto;
  letter-spacing: -1.5px;
  line-height: 30px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding: 15px;
  border-bottom: 1px solid rgba(255, 255, 0, 0.253);
  border-radius: 5%;
  transition: 0.3s;
}


.quote-btns-container {
  height: 50px;
  margin-top: 15px;

}
@media only screen and (max-width: 446px) {
  .quote-info-container {
    height: 250px;
  }
}

@media only screen and (max-width: 446px) and (min-height: 720px) {
  .quote-info-container {
    height: 350px;
  }
}


@media only screen and (min-width: 744px) {
  .quote-info-container {
    height: 280px;
  }
}
.btn-next-container {
  /* position: absolute;
  right: 3%; */
  float: right;
}

.btn-next-container button {
  padding-right: 20px;
  margin-right: 15px;
  box-shadow: -2px 2px 10px rgba(83, 74, 74, 0.39);
}

.btns button {
  cursor: pointer;
  font-size: 44px;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-content: center;
  outline: none;
  color: rgba(233, 223, 204, 0.767);
  font-weight: bold;
  padding: 0px 17px 3px 17px;
  /* background: linear-gradient(to left, rgb(44, 25, 25),  rgb(28, 28, 100) ); */
  text-shadow:  2px 1px 8px rgba(54, 121, 197, 0.527);
  border: 2px solid transparent;
  background-color: transparent;
  transition: 0.5s ease-in-out;

}
.btns button:hover {
  color: rgb(19, 202, 19);
  border: 2px solid green;
}

.btns button:active {
  border: 2px solid rgb(0, 255, 170);
  color: rgb(15, 199, 107);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);

  transition: 0.1s;
}
